<template>
  <div id="main">
    <div id="header">
      <HeaderComponent />
    </div>

    <div id="content">
      <div v-if="this.loading" class="d-flex align-items-center container">
        <strong>Loading...</strong>
        <div
          class="spinner-border ms-auto"
          role="status"
          aria-hidden="true"
        ></div>
      </div>
      <div v-else class="sidebar-menu-container" id="sidebar-menu-container">
        <div class="sidebar-menu-push">
          <div class="sidebar-menu-overlay"></div>
          <div class="sidebar-menu-inner">
            <section class="courses-grid" style="padding-top: 0px">
              <div class="container">
                <div class="row">
                  <div class="col-md-4" v-for="post in posts" :key="post.id">
                    <div class="slider owl-carousel mt-5">
                      <div class="card">
                        <div class="img"><img :src="post.image" alt="" /></div>
                        <div class="content">
                          <div class="title">
                            {{ post.subjectId }} {{ post.subjectName }}
                          </div>

                          <div class="sub-title">
                            <span class="dot-1"></span> {{ post.subjectType }}
                          </div>
                          <p :inner-html.prop="post.title | truncate(150)"></p>
                          <div class="btn">
                            <router-link
                              :to="{
                                name: 'course_id',
                                params: { id: post.id },
                              }"
                              ><button>View more</button></router-link
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-12">
                    <div class="pagination-navigation">
                      <div class="row">
                        <div class="col-md-6 col-xs-6">
                          <div class="pagination">
                            <ul>
                              <li
                                v-for="n in pagination.last_page"
                                :key="n"
                                :class="{
                                  active: pagination.current_page == n,
                                }"
                                class="page-item"
                              >
                                <a
                                  @click="DataPost(pagination.path_page + n)"
                                  class="one"
                                  style="cursor: pointer"
                                >
                                  {{ n }}</a
                                >
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div class="col-md-6 col-xs-6">
                          <div class="navigation">
                            <ul>
                              <li
                                class="page-item"
                                style="cursor: pointer"
                                :class="{
                                  disabled:
                                    pagination.current_page ==
                                    pagination.last_page,
                                }"
                              >
                                <a v-if="pagination.current_page == 1"
                                  ><i class="fa fa-arrow-left"></i
                                ></a>
                                <a
                                  v-else
                                  @click="
                                    DataPost(
                                      pagination.path_page +
                                        pagination.prev_link
                                    )
                                  "
                                  ><i class="fa fa-arrow-left"></i
                                ></a>
                              </li>
                              <li class="page-item">
                                <a
                                  v-if="
                                    pagination.current_page ==
                                    pagination.last_page
                                  "
                                  ><i class="fa fa-arrow-right"></i
                                ></a>
                                <a
                                  v-else
                                  @click="
                                    DataPost(
                                      pagination.path_page +
                                        pagination.next_link
                                    )
                                  "
                                  ><i class="fa fa-arrow-right"></i
                                ></a>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <a
                  class="backbt mt-4"
                  type="button"
                  @click="$router.go(-1)"
                  style="text-decoration: none; color: white"
                  ><i class="fas fa-chevron-left"></i> Back
                </a>
              </div>
            </section>
            <a href="#" class="go-top"><i class="fa fa-angle-up"></i></a>
          </div>
        </div>
      </div>
    </div>
    <div id="footer">
      <FooterComponent />
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import HeaderComponent from "../../components/HeaderComponent.vue";
import FooterComponent from "../../components/FooterComponent.vue";
export default {
  name: "course",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      posts: {},
      pagination: {},
      loading: true,
    };
  },
  mounted() {
    this.DataPost();
  },
  methods: {
    DataPost(page) {
      page = page || "/api/subject_learning";
      fetch(page)
        .then((res) => res.json())
        .then((res) => {
          this.posts = res.data;
          this.pagination = {
            current_page: res.current_page,
            last_page: res.last_page,
            path_page: res.path + "?page=",
            prev_link: res.current_page - 1,
            next_link: res.current_page + 1,
          };
          this.loading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};

Vue.filter("truncate", function (value, length) {
  if (!value) return "";
  value = value.toString();
  if (value.length > length) {
    return value.substring(0, length) + "...";
  } else {
    return value;
  }
});
</script>

<style scoped>
.one {
  text-decoration: none;
}
#content {
  margin-top: 7%;
}

.slider {
  max-width: 1100px;
  display: flex;
}
.slider .card {
  flex: 1;
  margin: 0 10px;
  background: #fff;
}
.slider .card .img {
  height: 200px;
  width: 100%;
}
.slider .card .img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.slider .card .content {
  padding: 10px 20px;
}
.card .content .title {
  font-size: 25px;
  font-weight: 600;
}
.card .content .sub-title {
  font-size: 20px;
  font-weight: 600;
  color: #e74c3c;
  line-height: 20px;
}
.card .content p {
  text-align: justify;
  margin: 10px 0;
}
.card .content .btn button {
  background: #f36656;
  color: #fff;
  border: none;
  outline: none;
  font-size: 17px;
  padding: 5px 8px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}
.card .content .btn button:hover {
  transform: scale(0.9);
}
.backbt {
  width: 80px;
  height: 40px;
  background: #a12c2f;
  border-radius: 2px;
  float: right;
  text-align: center;
  line-height: 40px;
  color: white;
  font-size: 16px;
}
.backbt:hover {
  background: linear-gradient(135deg, #a12c2f, #e43c42);
}
</style>
