this.loading\" class=\"d-flex align-items-center container\">\r\n        <strong>Loading...</strong>\r\n        <div\r\n          class=\"spinner-border ms-auto\"\r\n          role=\"status\"\r\n          aria-hidden=\"true\"\r\n        ></div>\r\n      </div>\r\n      <div v-else class=\"sidebar-menu-container\" id=\"sidebar-menu-container\">\r\n        <div class=\"sidebar-menu-push\">\r\n          <div class=\"sidebar-menu-overlay\"></div>\r\n          <div class=\"sidebar-menu-inner\">\r\n            <section class=\"courses-grid\" style=\"padding-top: 0px\">\r\n              <div class=\"container\">\r\n                <div class=\"row\">\r\n                  <div class=\"col-md-4\" v-for=\"post in posts\" :key=\"post.id\">\r\n                    <div class=\"slider owl-carousel mt-5\">\r\n                      <div class=\"card\">\r\n                        <div class=\"img\"><img :src=\"post.image\" alt=\"\" /></div>\r\n                        <div class=\"content\">\r\n                          <div class=\"title\">\r\n                            {{ post.subjectId }} {{ post.subjectName }}\r\n                          </div>\r\n\r\n                          <div class=\"sub-title\">\r\n                            <span class=\"dot-1\"></span> {{ post.subjectType }}\r\n                          </div>\r\n                          <p :inner-html.prop=\"post.title | truncate(150)\"></p>\r\n                          <div class=\"btn\">\r\n                            <router-link\r\n                              :to=\"{\r\n                                name: 'course_id',\r\n                                params: { id: post.id },\r\n                              }\"\r\n                              ><button>View more</button></router-link\r\n                            >\r\n                          </div>\r\n                        </div>\r\n                      </div>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n                <div class=\"row\">\r\n                  <div class=\"col-md-12\">\r\n                    <div class=\"pagination-navigation\">\r\n                      <div class=\"row\">\r\n                        <div class=\"col-md-6 col-xs-6\">\r\n                          <div class=\"pagination\">\r\n                            <ul>\r\n                              <li\r\n                                v-for=\"n in pagination.last_page\"\r\n                                :key=\"n\"\r\n                                :class=\"{\r\n                                  active: pagination.current_page == n,\r\n                                }\"\r\n                                class=\"page-item\"\r\n                              >\r\n                                <a\r\n                                  @click=\"DataPost(pagination.path_page + n)\"\r\n                                  class=\"one\"\r\n                                  style=\"cursor: pointer\"\r\n                                >\r\n                                  {{ n }}</a\r\n                                >\r\n                              </li>\r\n                            </ul>\r\n                          </div>\r\n                        </div>\r\n                        <div class=\"col-md-6 col-xs-6\">\r\n                          <div class=\"navigation\">\r\n                            <ul>\r\n                              <li\r\n                                class=\"page-item\"\r\n                                style=\"cursor: pointer\"\r\n                                :class=\"{\r\n                                  disabled:\r\n                                    pagination.current_page ==\r\n                                    pagination.last_page,\r\n                                }\"\r\n                              >\r\n                                <a v-if=\"pagination.current_page == 1\"\r\n                                  ><i class=\"fa fa-arrow-left\"></i\r\n                                ></a>\r\n                                <a\r\n                                  v-else\r\n                                  @click=\"\r\n                                    DataPost(\r\n                                      pagination.path_page +\r\n                                        pagination.prev_link\r\n                                    )\r\n                                  \"\r\n                                  ><i class=\"fa fa-arrow-left\"></i\r\n                                ></a>\r\n                              </li>\r\n                              <li class=\"page-item\">\r\n                                <a\r\n                                  v-if=\"\r\n                                    pagination.current_page ==\r\n                                    pagination.last_page\r\n                                  \"\r\n                                  ><i class=\"fa fa-arrow-right\"></i\r\n                                ></a>\r\n                                <a\r\n                                  v-else\r\n                                  @click=\"\r\n                                    DataPost(\r\n                                      pagination.path_page +\r\n                                        pagination.next_link\r\n                                    )\r\n                                  \"\r\n                                  ><i class=\"fa fa-arrow-right\"></i\r\n                                ></a>\r\n                              </li>\r\n                            </ul>\r\n                          </div>\r\n                        </div>\r\n                      </div>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n                <a\r\n                  class=\"backbt mt-4\"\r\n                  type=\"button\"\r\n                  @click=\"$router.go(-1)\"\r\n                  style=\"text-decoration: none; color: white\"\r\n                  ><i class=\"fas fa-chevron-left\"></i> Back\r\n                </a>\r\n              </div>\r\n            </section>\r\n            <a href=\"#\" class=\"go-top\"><i class=\"fa fa-angle-up\"></i></a>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n    <div id=\"footer\">\r\n      <FooterComponent />\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\n// import axios from \"axios\";\r\nimport HeaderComponent from \"../../components/HeaderComponent.vue\";\r\nimport FooterComponent from \"../../components/FooterComponent.vue\";\r\nexport default {\r\n  name: \"course\",\r\n  components: { HeaderComponent, FooterComponent },\r\n  data() {\r\n    return {\r\n      posts: {},\r\n      pagination: {},\r\n      loading: true,\r\n    };\r\n  },\r\n  mounted() {\r\n    this.DataPost();\r\n  },\r\n  methods: {\r\n    DataPost(page) {\r\n      page = page || \"/api/subject_learning\";\r\n      fetch(page)\r\n        .then((res) => res.json())\r\n        .then((res) => {\r\n          this.posts = res.data;\r\n          this.pagination = {\r\n            current_page: res.current_page,\r\n            last_page: res.last_page,\r\n            path_page: res.path + \"?page=\",\r\n            prev_link: res.current_page - 1,\r\n            next_link: res.current_page + 1,\r\n          };\r\n          this.loading = false;\r\n        })\r\n        .catch((err) => {\r\n          console.log(err);\r\n        });\r\n    },\r\n  },\r\n};\r\n\r\nVue.filter(\"truncate\", function (value, length) {\r\n  if (!value) return \"\";\r\n  value = value.toString();\r\n  if (value.length > length) {\r\n    return value.substring(0, length) + \"...\";\r\n  } else {\r\n    return value;\r\n  }\r\n});\r\n</script>\r\n\r\n<style scoped>\r\n.one {\r\n  text-decoration: none;\r\n}\r\n#content {\r\n  margin-top: 7%;\r\n}\r\n\r\n.slider {\r\n  max-width: 1100px;\r\n  display: flex;\r\n}\r\n.slider .card {\r\n  flex: 1;\r\n  margin: 0 10px;\r\n  background: #fff;\r\n}\r\n.slider .card .img {\r\n  height: 200px;\r\n  width: 100%;\r\n}\r\n.slider .card .img img {\r\n  height: 100%;\r\n  width: 100%;\r\n  object-fit: cover;\r\n}\r\n.slider .card .content {\r\n  padding: 10px 20px;\r\n}\r\n.card .content .title {\r\n  font-size: 25px;\r\n  font-weight: 600;\r\n}\r\n.card .content .sub-title {\r\n  font-size: 20px;\r\n  font-weight: 600;\r\n  color: #e74c3c;\r\n  line-height: 20px;\r\n}\r\n.card .content p {\r\n  text-align: justify;\r\n  margin: 10px 0;\r\n}\r\n.card .content .btn button {\r\n  background: #f36656;\r\n  color: #fff;\r\n  border: none;\r\n  outline: none;\r\n  font-size: 17px;\r\n  padding: 5px 8px;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  transition: 0.3s;\r\n}\r\n.card .content .btn button:hover {\r\n  transform: scale(0.9);\r\n}\r\n.backbt {\r\n  width: 80px;\r\n  height: 40px;\r\n  background: #a12c2f;\r\n  border-radius: 2px;\r\n  float: right;\r\n  text-align: center;\r\n  line-height: 40px;\r\n  color: white;\r\n  font-size: 16px;\r\n}\r\n.backbt:hover {\r\n  background: linear-gradient(135deg, #a12c2f, #e43c42);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
